export default {
	// components: { WucTab },
	data() {
		return {
			list:[],
			TabCur: 0,
			tabList: [{ name: '待审批' }, { name: '已同意' }, { name: '已驳回' }],
		}
	},
	onLoad() {
	 uni.request({
	 	url: 'https://unidemo.dcloud.net.cn/api/news',
	 	method: 'GET',
	 	data: {},
	 	success: res => {
			console.log(res)
			this.list = res.data
		},
	 	fail: () => {},
	 	complete: () => {}
	 });
	},
	methods: {
		openReport(e){
			// console.log(e)
			// let id = e.currentTarget.dataset.id
			uni.navigateTo({
				// url: '../detail/detail?id='+id
				url: './inventory'
			});
		},
		openInventory(e){
			uni.navigateTo({
				url: './inventory'
			});
		},
		openCash(e){
			uni.navigateTo({
				url: './cash'
			});
		},
		openPayment(e){
			uni.navigateTo({
				url: './payment'
			});
		},
		openCollection(e){
			uni.navigateTo({
				url: './collection'
			});
		},
		tabChange(index) {
			this.TabCur = index;
			console.log(this.TabCur,'sss')
		}
	}
